
import selectCity from '@/components/SelectCity'
import dateFormat from 'vux/src/tools/date/format.js'

export default {
  name: 'customerEdit',
  components: {
    dateFormat,
    selectCity
  },
  computed: {
    registTime () {
      return this.formatDate(this.form.customer.registTime)
    },
    expireTime () {
      return this.formatDate(this.form.customer.expireTime)
    }
  },
  data () {
    return {
      txt: '',
      form: {
        userId: '',
        customerId: '',
        customer: {
          areaFullName: '110010',
          carNo: '',
          driverCard: '',
          engine: '',
          expireTime: '',
          id: '',
          idCard: '',
          name: '',
          note: '',
          phone: '',
          registTime: '',
          subDriverCard: '',
          vin: ''
        }
      }
    }
  },
  created () {
    const This = this
    this.form.customerId = this.$route.params.customerId
    this.form.userId = this.$route.params.userId
    setTimeout(() => {
      This.customerInfo()
    }, 200)
  },
  methods: {
    customerInfo () {
      this.$http({
        method: 'jsonp',
        url: 'http://liuwbox.com/zzbao/app/customer/detail.htm',
        jsonp: 'callback',
        params: {
          customerId: this.form.customerId
        },
        jsonpCallback: 'json'
      })
      .then(res => {
        this.form.customer = res.body.data.customer
        this.txt = res.body.data.customer.areaFullName
      })
    },
    change () {
      this.form.customer = JSON.stringify(this.form.customer)
      this.$http({
        method: 'jsonp',
        url: 'http://liuwbox.com/zzbao/app/customer/submit.htm',
        jsonp: 'callback',
        params: this.form,
        jsonpCallback: 'json'
      })
      .then(res => {
      })
    },
    del () {
      this.form.customer = JSON.stringify(this.form.customer)
      this.$http({
        method: 'jsonp',
        url: 'http://liuwbox.com/zzbao/app/customer/del.htm',
        jsonp: 'callback',
        params: {
          userId: this.form.userId,
          customerId: this.form.customerId
        },
        jsonpCallback: 'json'
      })
      .then(res => {
      })
    },
    formatDate (date) {
      return dateFormat(date, 'YYYY-MM-DD')
    }
  }
}



