
import {mapMutations} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Cell from 'vux/src/components/cell/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XSwitch from 'vux/src/components/x-switch/index.vue'

import {area} from '../config'
export default {
  name: 'city',
  data () {
    return {
      tab: [{
        text: '省',
        id: 0
      }, {
        text: '市',
        id: 0
      }, {
        text: '区',
        id: 0
      }],
      selected: '',
      areaId: [],
      show: false,
      showName: '',
      index: 0,
      active: 0,
      areaList: []
    }
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: '请选择'
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  directives: {
    TransferDom
  },
  components: {
    Group,
    Popup,
    Cell,
    Tab,
    TabItem,
    XButton,
    Swiper,
    SwiperItem,
    XSwitch
  },
  methods: {
    submit () {
      let area = ''
      this.tab.forEach(el => {
        if (el.id !== 0) {
          area += el.text
        }
      })
      this.selected = area
      this.show = false
      this.init()
    },
    handleSwiper (areaId, text, index) {
      this.tab[index] = {
        id: areaId,
        text: text
      }
      this.areaId[index] = areaId
      if (index < 2) {
        this.index = this.active = index + 1
      } else {
        // let id = []
        // this.tab.forEach(el => {
        //   id.push(el.id)
        // })
        this.setAreaId(areaId)
        this.index = this.active = index
        let area = ''
        this.tab.forEach(el => {
          area += el.text
        })
        this.selected = area
        this.show = false
        this.init()
      }
      this.getData(areaId, text, index)
    },
    handleClick (index) {
      this.index = this.active = index
    },
    handleChange (index) {
      this.index = this.active = index
      if (index === 0) {
        this.getData(0, '请选择', 0)
      } else {
        this.getData(this.tab[index - 1].id, this.tab[index - 1].text, index)
      }
    },
    getData (areaId, text, index) {
      const This = this
      setTimeout(function () {
        This.$http({
          method: 'jsonp',
          url: area,
          jsonp: 'callback',
          jsonpCallback: 'json',
          params: {
            id: areaId
          }
        })
        .then(res => {
          This.areaList = res.body.data.areaList
        })
      }, 100)
    },
    handleOpen () {
      if (!this.readonly) {
        this.show = true
        this.getData(this.tab[0].id, this.tab[0].text, 0)
      }
    },
    init () {
      this.index = this.active = 0
      this.tab = [{
        text: '省',
        id: 0
      }, {
        text: '市',
        id: 0
      }, {
        text: '区',
        id: 0
      }]
    },
    ...mapMutations({
      setAreaId: 'getInsuranceArea'
    })
  },
  created () {
    this.showName = this.value
  }
}



